@import url(https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Patrick Hand', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-y: hidden;
  width: 100vw;
  height: 100vh;
  background-image: radial-gradient(circle, #f694c1, #ffa3a5, #fdba98, #edd09f, #dce4b7, #c8ebc7, #b9f0dc, #b2f3f2, #9eeaff, #ace0ff, #c5d2ff, #e4c1f9);

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --pink: #F694C1;
}

.navbar{
  background-color: whitesmoke;
  height: 3rem;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  position: fixed;
  bottom: 0;
  font-size: 1rem;
  padding: 0.2rem;
  z-index: 999;
}

.navbar > div >  img{
  height: 1.5rem;
  width: 1.5rem;
  padding: 0.5rem;
}

.current{
  background-color: #F694C1;
  background-color: var(--pink);
  border-radius: 100%;
}

div > a{
  display: flex;
  height: 1.5rem;
  width: 1.5rem;
  padding: 0.4rem;
}
.moodPic{
    width: 3rem;
    height: 3rem;
}
.Quiz-container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(4, 1fr);
}

.moodName{
    font-size: 0.7rem;
    color: white;
    margin: 0;
    padding: 0;
}

.mood-container{
    height: 4rem;
    width: 4rem;
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.245);
    padding: 0.7rem ;
    border-radius: 50%;
    margin: 0.2rem;
}

.chosen{
    background-color: rgba(0, 0, 0, 0.438);
}
.wrapper{
    height: 100vh;
    overflow-y: hidden;
    width: 100vw;
    display: flex; 
    flex-direction: column;
    flex-wrap: wrap;
    align-content: space-around;
    align-items: stretch;
    justify-content: center;
}
.page-title{
    color: white;
    box-shadow: inset;
    text-align: center;
    padding: 0.5rem;
}
.pink-button {
  background-color: #F694C1;
  border: none;
  color: white;
  padding: 0.7rem 3.5rem;
  text-align: center;
  text-decoration: none;
  display: block;
  font-size: 1.3rem;
  border-radius: 2rem;
  font-family: 'Patrick Hand', cursive;
 
}

.small-text {
  text-align: center;
  color: #fff;
  margin-top: 0.7rem;
  font-size: 1.1rem;
}
.webcam-wrapper {
    width: 100vw;
    height: 90vh;
    justify-content: space-around;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.taken-image {
    margin: 2rem;
}
.suggestPic{
    height: 10rem;
    width: 10rem;
}

.suggestor-wrapper{
    display: flex;
    width: 100vw;
    height: 80vh;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    color: azure;
}

.others-wrapper{
    display: flex;
    flex-direction: column;
    grid-gap: 0.5rem;
    gap: 0.5rem;
}

.suggestor-text{
    text-align: center;
}

.otherbtn{
    font-size: 0.9rem;
    padding: 0.3rem 2rem;
}

.suggestion-wrapper{
    display: flex;
    flex-direction: column;
    height: 80vh;
    justify-content: center;
    align-items: center;
}

.content{
    width: 90%;
    margin-left:auto;
    margin-right: auto;
}

.content > div {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}
 .content > div >img{
    width: 19rem;
 }

 .arrow{
    width: 1rem;
    padding: 1.3rem;
 }
.offline-wrapper{
    color: rgb(255, 255, 255);
    height: 88vh;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
    
}

.offline-wrapper > img{
    height: 40%;
}

